<template>

  <div>
    <div v-if="checkIdxList.length>0">
      <PdfModal :pdfNameList="pdfNameList" :checkIdxList="checkIdxList" @checkIdx="checking()"/>
    </div>
    <input type="checkbox" id="checkType1" v-model="requiredAgree" disabled/>
    <label @click="addAllToCheckIdxList()">전체 읽기</label><br/>
    <input type="checkbox" id="checkType1_1" value="1" v-model="checked" disabled />
    <label @click="addToCheckIdxList(1)">문서 A 읽기</label><br/>
    <input type="checkbox" id="checkType1_2" value="2" v-model="checked" disabled />
    <label @click="addToCheckIdxList(2)">문서 B 읽기</label><br/>
    <input type="checkbox" id="checkType1_3" value="3" v-model="checked" disabled />
    <label @click="addToCheckIdxList(3)">문서 C 읽기</label><br/>
    <button id="checkType1_r" @click="resetChecked()">되돌리기</button>
  </div>
</template>

<script>
import PdfModal from './components/PdfModal.vue';

export default {
  name: 'App',
  components: {
    PdfModal
  },
  data() {
    return {
      requiredAgree: false,
      pdfNameList: [
        ' ',
        '/pdfvuerprac/sampleA.pdf',
        '/pdfvuerprac/sampleB.pdf',
        '/pdfvuerprac/sampleC.pdf'
      ],
      /**체크 해야 할 문서 목록 */
      checkIdxList: [],
      /**체크를 한 문서 목록 */
      checked: []

    }
  },
  methods: {
    checking() {
      this.checked.push(String(this.checkIdxList[0])); 
      this.checkIdxList.shift(); 
      if (this.checked.includes('1') &&
          this.checked.includes('2') &&
          this.checked.includes('3') ) {
        this.requiredAgree = true;
      } else {
        this.requiredAgree = false;
      }
    },
    addAllToCheckIdxList() {
      let remainingIdx = [];
      for (let i=1; i<=3; i++) {
        if (!this.checked.includes(String(i))) remainingIdx.push(i);
      }
      this.checkIdxList = remainingIdx;
    },
    addToCheckIdxList(idx) {
      this.checkIdxList.push(idx);
    },
    resetChecked() {
      this.requiredAgree = false;
      this.checked = [];
      this.checkIdxList = [];
    }
  }
}
</script>

