<template>
  <div class="black-bg" ref="scroll">
    <div class="white-bg">
        <pdf
            v-for="i in numPages"
            :key="i"
            :src="src"
            :page="i"
            style="display: block"></pdf>
        <div class="olive-bg" @click="getGoing()">넘어가기</div>
    </div>
  </div>
</template>

<script>
import pdf from 'vue-pdf';

export default {
    components: {
        pdf
    },
    props: {
        checkIdxList:Array,
        pdfNameList:Array
    },
    methods: {
        pdfName() {
            let result = `${this.pdfNameList[Number(this.checkIdxList[0])]}`;
            //alert(result);
            return result;
        },
        getGoing() {
            this.$emit('checkIdx');
            this.$refs.scroll.scrollTo({top: 0, behavior: 'smooth'});
            this.getNumPages();
        },
        getNumPages() {
            let loadingTask = pdf.createLoadingTask(this.pdfName());
            this.src = loadingTask;
            this.src.promise.then(pdf => {
                this.numPages = pdf.numPages;
            });
        }
    },
    data() {
        return {
            src: undefined,
            numPages: undefined
        }
    },
    mounted() {
        this.getNumPages();
    }
}
</script>

<style>
.black-bg{
    width: 90%;
    height: 70%;
    overflow-y: scroll;
    background-color:rgba(0, 0, 0, 0.75);
    position: fixed;
    padding: 10px;
}
.white-bg{
    width: 100%;
    background-color: white;
    border-radius: 8px;
    padding: 20px;
}
.olive-bg{
    text-align: center;
    font-size: 16px;
    width: 100%;
    background-color: olive;
    height: 50px;
}

</style>